import "bootstrap/dist/css/bootstrap.min.css";
import {
  Container,
  Image,
  Form,
  Navbar,
  Nav,
  Button,
  Col,
} from "react-bootstrap";
import BackgroundContainer from "./components/BackgroundContainer";
import ContactFooter from "./components/ContactFooter";
import Logo from "./assets/img/Main/Logo.png";
import { useRef, useState } from "react";
import { HashRouter, Switch, Route } from "react-router-dom";

import {Helmet} from 'react-helmet-async';
//import { Helmet } from "react-helmet";
import { AnimatePresence, motion } from "framer-motion";

import Fab from "./components/Fab";
import Cards from "./components/Cards";
import CardsGallery from "./components/CardsGallery";

import Lightbox from "react-awesome-lightbox";
// You need to import the CSS only once
import "react-awesome-lightbox/build/style.css";


function App() {
  const myRef = useRef(null);
  const executeScroll = () =>
    myRef.current.scrollIntoView({
      behavior: "smooth",
    });

    const [commentList,setCommentList]=useState({})

  return (
    <HashRouter basename="/" className="App">
      <Helmet>
        <title>Installationstjänster Stockholm, Väsby - Petrosson Bygg</title>
        {/*<link rel="canonical" href="http://mysite.com/example" />*/}

        <meta
          name="description"
          content="Professionella dekoration och installationstjänster.
          dörrmontering,golvmontering,köksmontering,vaxhouse,altanmontering,terrassmontering och renovering i Upplands Väsby, Stockholm"
        />
        <meta
          name="keywords"
          content="montering,golvmontering,dörrsmontering,köksmontering,altanmontering,terrassmontering,renovering,vaxhouse,Installationstjänster Stockholm, terrassinstallation,vaxhouseinstallation , Heminredning, dekoration, installationstjänster, Upplands Väsby, Stockholms län, Petrosson BYGG, PetrossonBYGG, decoration stockholm, installation stockholm, Dörr,altan, golv, kök, installationstjänster stockholm, stockholm installationstjänster"
        />

        <meta name="author" content="Boutros Tawaifi" />
        <meta
          name="title"
          property="og:title"
          content="Installationstjänster Stockholm, Väsby - PetrossonBygg"
        />
        <meta
          name="image"
          property="og:image"
          content="./assets/img/Logo.png"
        />
      </Helmet>

      <AnimatePresence exitBeforeEnter>
        <div>
          <Navbar bg="light" variant="light">
            
            <Image className="Logo" onClick={()=>window.location.href='/#'} src={Logo} width="105" height="35" />
            
            <Nav className="mr-auto"></Nav>
            <Form inline>
              <Button
                onClick={executeScroll}
                variant="primary"
                style={{ borderRadius: 20 }}
              >
                Contact
              </Button>
            </Form>
          </Navbar>

          <Container fluid style={{ padding: 0 }}>
            <Title BgColor={"bg"} />

            <Switch key={window.location.pathname}>
              <Route exact path="/">
                <Home />
              </Route>

              <Route exact path="/Kok">
                <Kok />

              </Route>

              <Route exact path="/Golv">
                <Golv />
              </Route>

              <Route exact path="/Dorr">
                <Dorr />
              </Route>

              <Route exact path="/Terrass">
                <Terrass />
              </Route>

              <Route exact path="/VaxHouse">
                <VaxHouse />
              </Route>

              <Route exact path="/Badrum">
                <Badrum />
              </Route>

              <Route exact path="/Bastu">
                <Bastu />
              </Route>

              <Route exact path="/Altan">
                <Altan />
              </Route>

              <Route exact path="/Test">
                <Test />
              </Route>
            </Switch>
          </Container>

          <span ref={myRef}></span>
          <ContactFooter />
        </div>
      </AnimatePresence>
    </HashRouter>
  );
}

function Test(props) {
  let images = [
    {
        url:"https://www.thespruce.com/thmb/0qJKb8sMZuyUVNnMeytwUBoyIY0=/2121x1414/filters:fill(auto,1)/Whitemodernkitchen-GettyImages-1089101352-4eddd67a46984affa521c889b02c5bf1.jpg",
        title:"image title 1"
    },
    {
        url:"https://www.thespruce.com/thmb/0qJKb8sMZuyUVNnMeytwUBoyIY0=/2121x1414/filters:fill(auto,1)/Whitemodernkitchen-GettyImages-1089101352-4eddd67a46984affa521c889b02c5bf1.jpg",
        title:"image title 2"
    }
  ]

  return (
    <BackgroundContainer onClose={()=>window.location.href='/#'} BgColor={"bg_images"}>
      <Lightbox images={images}/>
    </BackgroundContainer>
  );
}


function Title(props) {
  return (
    <BackgroundContainer BgColor={props.BgColor}>
      <div className="bg_container_text ml-auto">
        <Col>
          <h1 style={{ fontFamily: "MY_futur" }}>PetrossonBYGG</h1>
          <h4 style={{ fontFamily: "MY_futurLt" }}>Professionella dekoration och installationstjänster</h4>
          <h3 style={{ fontFamily: "MY_futur" }}>Upplands Väsby, Stockholm</h3>
        </Col>
      </div>
    </BackgroundContainer>
  );
}

function Home() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <Cards />
    </BackgroundContainer>
  );
}

function Kok() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"Kok"} />
      <Fab style={{ marginBottom: 30 }} />
      
    </BackgroundContainer>
  );
}

function Golv() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"Golv"} />
      <Fab style={{ marginBottom: 30 }} />
    </BackgroundContainer>
  );
}

function Dorr() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"Dorr"} />
      <Fab style={{ marginBottom: 30 }} />
    </BackgroundContainer>
  );
}
function Terrass() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"Terrass"} />
      <Fab style={{ marginBottom: 30 }} />
    </BackgroundContainer>
  );
}
function VaxHouse() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"VaxHouse"} />
      <Fab style={{ marginBottom: 30 }} />
    </BackgroundContainer>
  );
}
function Badrum() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"Badrum"} />
      <Fab style={{ marginBottom: 30 }} />
    </BackgroundContainer>
  );
}
function Bastu() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"Bastu"} />
      <Fab style={{ marginBottom: 30 }} />
    </BackgroundContainer>
  );
}
function Altan() {
  return (
    <BackgroundContainer BgColor={"bg_images"}>
      <CardsGallery setCards={"Altan"} />
      <Fab style={{ marginBottom: 30 }} />
    </BackgroundContainer>
  );
}

export default App;
