import React from 'react'
import Container from 'react-bootstrap/Container'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAt, faMapMarkerAlt, faMobileAlt } from '@fortawesome/free-solid-svg-icons'

const ContactFooter = () => {
    return (
        <Container fluid className='footer main-text-color mt-5 mb-5 custom-headers' style={{ fontSize: '0.9rem' }}>
            <h1 className='mb-5'>kontakta oss</h1>
            <div>
                <div className='mb-3'>
                    <FontAwesomeIcon icon={faAt} size='2x' fixedWidth />
                    <h2 style={{ display: 'inline' }}> Tawaifi@yahoo.com</h2>
                </div>
                <div className='mb-3' >
                    <FontAwesomeIcon icon={faMobileAlt} size='2x' fixedWidth />
                    <h2 style={{ display: 'inline' }}> 0739273930</h2>
                </div>
                <div className='mb-3'>
                    <FontAwesomeIcon icon={faMapMarkerAlt} size='2x' fixedWidth />
                    <h2 style={{ display: 'inline' }}> Upplands Väsby 194 37, Sweden</h2>
                    <h2 className='ml-3' style={{ display: 'block', paddingLeft: 13 }}> Hasselgatan 19</h2>
                </div>
            </div>

        </Container >
    )
}

export default ContactFooter;