import React from 'react'
import { Card, Button } from 'react-bootstrap';
import { motion } from 'framer-motion';
function Cards(props) {
    return (
        <>
        <h4 className='galleryTitle'>
        Våra tjänster
         </h4>
        <motion.div className='CardContainer'
            initial={{ x: "-50vh" }}
            animate={{ x: 0 }}
            >            

            <Card>
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/Kitchen.webp").default} />
                <Card.Body>
                    <Button href="/#/Kok" value='Kok' variant="outline-primary" size='lg'>Kök Galleri</Button>
                </Card.Body>
            </Card>
            <Card >
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/floor_Pic.webp").default} />
                <Card.Body>
                    <Button href="/#/Golv" value='Golv' variant="outline-primary" size='lg'>Golv Galleri</Button>
                </Card.Body>
            </Card>
            <Card>
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/door_Pic.webp").default} />
                <Card.Body>
                    <Button href="/#/Dorr" value='Dorr' variant="outline-primary" size='lg'>Dörr Galleri</Button>
                </Card.Body>
            </Card>
            <Card>
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/Terrace_Pic.webp").default} />
                <Card.Body>
                    <Button href="/#/Terrass" value='Terrass' variant="outline-primary" size='lg'>Terrass Galleri</Button>
                </Card.Body>
            </Card>
            <Card>
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/vaxhouse.webp").default} />
                <Card.Body>
                    <Button href="/#/VaxHouse" value='VaxHouse' variant="outline-primary" size='lg'>Vaxhouse Galleri</Button>
                </Card.Body>
            </Card>
            <Card>
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/Badrum_960.jpg").default} />
                <Card.Body>
                    <Button href="/#/Badrum" value='Badrum' variant="outline-primary" size='lg'>Badrum Galleri</Button>
                </Card.Body>
            </Card>
            <Card>
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/Bastu_960.jpg").default} />
                <Card.Body>
                    <Button href="/#/Bastu" value='Bastu' variant="outline-primary" size='lg'>Bastu Galleri</Button>
                </Card.Body>
            </Card>
            {/*<Card>
                <Card.Img className="custom-card-img" variant="top" src={require("../assets/img/Main/altan_960.jpg").default} />
                <Card.Body>
                    <Button href="/#/Altan" value='Altan' variant="outline-primary" size='lg'>Altan Galleri</Button>
                </Card.Body>
            </Card>*/}
        </motion.div >
        </>
    )
}

export default Cards;