import React, { useEffect, useState, useRef } from "react";
//import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
//import { faStar } from "@fortawesome/free-solid-svg-icons";
import Axios from "axios";
import moment from "moment";
import { loadCaptchaEnginge,LoadCanvasTemplateNoReload, validateCaptcha } from 'react-simple-captcha';

function AddComment(Destination, Elements) {
  return new Promise((resolve, reject) => {
    Axios.post(`${Destination}`, Elements)
      .then(() => {
        resolve();
      })
      .catch((err) => {
        console.log(err);
        reject();
      });
  });
}

function GetComment(Destination) {
  return new Promise((resolve, reject) => {
    Axios.get(`${Destination}`)
      .then((req) => resolve(req.data))
      .catch((err) => {
        console.log(err);
        reject();
      });
  });
}

const Comments = (props) => {
  const [commentsEmpty, setCommentsEmpty]=useState(0);
  const [commentsList, setCommentsList] = useState([{}]);
  const [commentNew, setCommentNew] = useState({});
  const commentStyle = {
    whole: {
      marginBottom: "20px",
    },
    name: {
      color: "white",
      fontSize: "20px",
      fontWidth: "bolder",
      marginBottom: "0px",
    },
    body: {
      color: "white",
      marginBottom: "0px",
      fontFamily: "MY_futurBody",
      fontSize: "18px",
    },
    star: {
      paddingRight: "10px",
      fontSize: "8px",
      display: "inline",
    },
    rest: {
      fontSize: "16px",
      color: "grey",
      padding: "0px",
      margin: "0px",
      fontFamily: "MY_futurLt",
    },
    Lists: {
      paddingLeft: "0px",
    },
  };

  const [loaded, setLoaded] = useState(0);
  useEffect(() => {
    loadCaptchaEnginge(6);
    // console.log(commentsList)
    GetComment(`${props.baseUrl}${props.route}`)
      .then((result) => {
        console.log(result)
        if(result==="Empty"){
          setCommentsEmpty(1)
        }else{
          if(result.length>0){
            setCommentsList(result)
            setCommentsEmpty(0)
          }
        }
      }
      )
      .then(() => setLoaded(1));
    return () => {};
  }, []);

  useEffect(() => {
    //loaded solves the async problem that useState makes by using a promise
    if (loaded === 1) {
      AddComment(`${props.baseUrl}${props.route}`, {
        commentNew: commentNew,
      });
    }
  }, [commentNew]);

  const ref_Input = useRef(null)
  const ref_textArea = useRef(null)
  const ref_captcha = useRef(null)

  const doSubmit = () => {
    let user_captcha = document.getElementById('user_captcha_input').value;

    if (validateCaptcha(user_captcha)===true) {
        loadCaptchaEnginge(6); 
        onAddComment();
    }

    else {
        alert('Captcha Does Not Match');
    }
};



  const commentItems = () => {
    if(commentsEmpty===0)
    return commentsList.map((item, index) => (
      <li key={index} style={commentStyle.whole} variants={item}>
        <div className="comments__content x-st">
          <div style={commentStyle.name}>{item.fullName}</div>

          <div>
            {/*
                <div style={commentStyle.star}>
                  <span className="x-nub">
                    <FontAwesomeIcon icon={faStar} size='2x' fixedWidth />
                  </span>
                  <span style={{fontSize:'medium'}} aria-hidden="true">
                  {item.stars}
                  </span>
                <span className="x-nuk" aria-hidden="true"></span>
                </div>*/}
            <time style={commentStyle.rest}>{item.date}</time>
          </div>
          <div style={commentStyle.body}>{item.text}</div>
        </div>
      </li>
    ));
  };

  const [commentFullName, setCommentFullName] = useState("");
  const [commentText, setCommentText] = useState("");

  const onAddComment = () => {
    setCommentsEmpty(0);
    setCommentsList([
      ...commentsList.map((item) => {
        return item;
      }),
      {
        fullName: commentFullName,
        text: commentText,
        stars: "5",
        date: moment().format("MM/DD/YYYY"),
      },
    ]);
    setCommentNew(
      {
        fullName: commentFullName,
        text: commentText,
        stars: "5",
        date: moment().format("MM/DD/YYYY"),
      },
    );
    ref_Input.current.value='';
    ref_textArea.current.value='';
    ref_captcha.current.value='';
    /*setCommentsList([
      ...commentsList.map((item) => {
        return item;
      }),
      {
        fullName: commentFullName,
        text: commentText,
        stars: "5",
        date: moment().format("MM/DD/YYYY"),
      },
    ]);*/
  };
  return (
    <section className="comments">
      <h2>Comments</h2>
      <ul style={commentStyle.Lists}>{commentItems()}</ul>

      <div>Add a new comment</div>
      <div>
        <input ref={ref_Input} placeholder='Name'
          onChange={(event) => setCommentFullName(event.target.value)}
        ></input>
      </div>
      <div>
        <textarea ref={ref_textArea} cols="35" rows="3" placeholder='Comment'
          onChange={(event) => setCommentText(event.target.value)}
        ></textarea>
      </div>
      <div>

        <div >
                <LoadCanvasTemplateNoReload/>
         </div>
         <div >
              <input ref={ref_captcha} placeholder="Enter Captcha" id="user_captcha_input" name="user_captcha_input" type="text"></input>
         </div>
        

        <button onClick={doSubmit} className="btn btn-primary">
          Post comment
        </button>
      </div>
    </section>
  );
};

export default Comments;
