import React, { useEffect,useState } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCaretLeft } from '@fortawesome/free-solid-svg-icons'
import { motion } from 'framer-motion';

const Fab=()=>{
    const [opacity,setOpacity] = useState(1);

    const windowScroll=()=>{
        window.onscroll = () => {
            let currentScrollPos = window.pageYOffset;
            let maxScroll = document.body.scrollHeight - window.innerHeight;
            let footerSize = document.querySelector('.footer').scrollHeight;
            let bgImagesSize = document.querySelector('.bg_images').scrollHeight;
    
            if (currentScrollPos > maxScroll - footerSize - bgImagesSize && currentScrollPos < maxScroll - footerSize) {
                setOpacity(1)
            }
            else {
                setOpacity(0)
            }
        }
    }

    useEffect(() => {
        //window.scrollTo(0, 0);
          window.addEventListener('scroll', windowScroll);
        return () => {
            setOpacity(1)
          window.removeEventListener('scroll', windowScroll);
        }
      }, [])


        return (
            <motion.div onClick={()=>window.location.href='/#'} whileHover={{ scale: 1.2 }} whileTap={{ scale: 0.8 }} className="Fab" style={{ opacity: `${opacity}` }} >
                <FontAwesomeIcon icon={faCaretLeft} size='3x' style={{ marginLeft: -6 }} />
            </motion.div>
        )
}

export default Fab