import React, { useState} from "react";
import { Card } from "react-bootstrap";
import Modal from "./Modal";
import { motion } from "framer-motion";

//import ImageGrid from './ImageGrid';
//import UploadForm from './UploadForm';
//import { projectFirestore } from '../firebase/config';

import ProgressiveImage from "react-progressive-image";

import "photoswipe/dist/photoswipe.css";
import "photoswipe/dist/default-skin/default-skin.css";
import { Gallery, Item } from "../external/react-photoswipe-gallery";

import Comments from "./Comments"


///this best of the best of all mappingz
function importAll(r) {
  let images = {};
  r.keys().map((item, index) => {
    return images[item.replace("./", "")] = r(item);
  });
  return images;
}

const images = importAll(
  require.context("../assets/img/", true, /\.(png|gif|ico|jpg|jpeg|webp)$/)
);

function loadPics(Addr, FolderName, MaxIndex, extension) {
  let PC = [];
  for (let i = 1; i <= MaxIndex; i++) {
      const Loc = images[Addr + "/" + FolderName + "/" + i + extension].default;

      const holder = (
        <motion.div
          initial={{ x: -100, scale: 0 }}
          animate={{
            x: 0,
            y: 0,
            scale: 1,
            rotate: 0,
          }}
        >
          <Card className="gallaryCard">
            {/*<img className='gallaryCardImg' sizes="(max-width: 640px) calc(100vw - 10px), 640px" src={Loc} srcSet={`${Loc} 640w, ${'/img/' + Addr + '/' + FolderName + '/' + i + '_1400' + extension} 1400w, ${'/img/' + Addr + '/' + FolderName + '/' + i + '_960' + extension} 960w)`} alt="Galleri Image" />
             */}

            <ProgressiveImage
              // src="medium.jpg"
              src={Loc}
              srcSetData={{
                //srcSet: 'small.jpg 320w, medium.jpg 700w, large.jpg 2000w',
                srcSet: `${Loc} 640w, ${
                  images[Addr + "/" + FolderName + "/" + i + "_960" + extension]
                    .default
                } 960w, ${
                  images[Addr + "/" + FolderName + "/" + i + "_1400" + extension]
                    .default
                } 1400w`,
                sizes: "(max-width: 1400px) 100vw, 1400px",
              }}
              placeholder={images["Main/placeholder.png"].default}
            >
              {(src, _loading, srcSetData) => (
                <img
                  className="gallaryCardImg"
                  src={src}
                  srcSet={srcSetData.srcSet}
                  sizes={srcSetData.sizes}
                  alt="Galleri"
                />
              )}
            </ProgressiveImage>
          </Card>
        </motion.div>
      );
      
      const holdGal = (
        <motion.div>
          <Card>
            <Item 
            original={Loc} 
            thumbnail={Loc} 
            width="720" 
            height="960">
              {({ ref, open }) => <img height={'auto'} width={'100%'} ref={ref} onClick={open} src={Loc} alt="Galleri img" />}
            </Item>
          </Card>
        </motion.div>
      );

      PC.push(holder);
  }
  return PC.map((item, index) => (
    <li key={index} className="item" variants={item}>
      {item}
    </li>
  ));
}

